<template>
  <div>
    <el-menu style="" class="leftNav" unique-opened>
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <div style="margin-left: 13vw; padding-top: 2vh">
      <h3 style="color: white">邮件总列表 站内+站外</h3>
      <el-button type="primary" @click="serchall()">全 部</el-button>
      <el-button type="primary" @click="searchIn()">站内邮件</el-button>
      <el-button type="primary" @click="searchOut()">站外邮件</el-button>
      <table class="emailTable">
        <thead>
          <tr>
            <th>序号</th>
            <th>收件时间</th>
            <th>渠道</th>
            <th>站点</th>
            <th>国家</th>
            <th>订单号</th>
            <th>问题类型</th>
            <th>附件</th>
            <th>回复情况</th>
            <th>处理时间</th>
            <th>处理人</th>
            <th>类型</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(item, index) in tableData"
            :key="index"
            @click="checkEmailDetail(item.order_number, item.fileLI)"
          >
            <td>{{ index + 1 + (page - 1) * 50 }}</td>

            <td>{{ item.email_date }}</td>

            <td>{{ item.platform }}</td>
            <td>{{ item.site }}</td>
            <td>{{ item.country }}</td>
            <td>{{ item.order_number }}</td>
            <td>{{ item.problem_type }}</td>
            <td>
              <a
                v-for="(item2, index2) in item.fileLI"
                :key="index2"
                download
                :href="item2"
                target="_Blank"
                >{{ getfilename(item2) }} <br
              /></a>
            </td>
            <td
              v-html="
                item.reply_situation == '已回复'
                  ? '已回复'
                  : item.type == '站内'
                  ? '<a href=/AmazonBuyerNews>未回复</a>'
                  : '<a href=/InboundMail>未回复</a>'
              "
            ></td>
            <td>{{ item.processing_time }}</td>
            <td>{{ item.dispose_person }}</td>
            <td>{{ item.type }}</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 35vw"
      >
      </el-pagination>
    </div>
    <footer style="margin-left: 13vw; display: none">
      <div
        style="
          background: rgba(255, 255, 255, 0.9);
          border: 1px solid #dddddd;
          padding: 0 0 1vh 1vh;
          border-radius: 10px;
          width: 60vw;
          margin-bottom: 2vh;
          position: relative;
        "
      >
        <div>
          订单编号：<el-input
            v-model="orderId"
            style="width: 15vw"
            placeholder="请输入内容"
            readonly
          ></el-input>
          收取时间：<el-input
            v-model="charingTime"
            style="width: 15vw"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>
        <div>
          站点：<el-input
            v-model="site"
            style="width: 10vw"
            placeholder="请输入内容"
            readonly
          ></el-input>
          国家：<el-input
            v-model="country"
            style="width: 10vw"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>
        <div>
          商品及数量：
          <el-input
            v-model="goodAndMount"
            style="width: 15vw"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>
        <div>
          买家消息内容：
          <el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="buysText"
            maxlength="9999"
            show-word-limit
            style="width: 15vw"
            :rows="7"
            readonly
          >
          </el-input>
        </div>
        <div>
          使用的回复模板：<el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="cloneUse"
            maxlength="9999"
            show-word-limit
            style="width: 15vw"
            :rows="7"
            readonly
          >
          </el-input>
        </div>
        <div>
          问题类型：<el-input
            style="width: 10vw"
            v-model="problemType"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>
        <div>
          缺件破损情况：<el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="lessAndBroken"
            maxlength="9999"
            show-word-limit
            style="width: 15vw"
            :rows="7"
            readonly
          >
          </el-input>
        </div>
        <div>
          关联操作：<el-input
            style="width: 10vw"
            v-model="association"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>
        <div>
          客户评级：<el-input
            style="width: 10vw"
            v-model="whiteOrBlack"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>
        <div>
          是否获得好评：<el-input
            style="width: 10vw"
            v-model="badOrGood"
            placeholder="请输入内容"
            readonly
          ></el-input>
        </div>

        <span class="buysInterpret">
          <el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="buysInterpret"
            maxlength="9999"
            show-word-limit
            style="width: 15vw"
            :rows="7"
            readonly
          >
          </el-input>
        </span>
        <span class="cloneInterpret">
          <el-input
            type="textarea"
            placeholder="请输入内容"
            v-model="cloneInterpret"
            maxlength="9999"
            show-word-limit
            style="width: 15vw"
            :rows="7"
            readonly
          >
          </el-input>
        </span>
        <span class="accessory"></span>
        <el-button
          type="danger"
          style="margin-left: 26vw"
          @click="closeFooter()"
          >关 闭</el-button
        >
      </div>
    </footer>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      leftNavData: [], //左侧导航栏
      tableData: [], //表格数据
      firstNav: "", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航
      total: 0, //页码当前页
      page: 1, //
      orderId: "", //订单编号
      site: "", //站点
      country: "", //国家
      goodAndMount: "", //商品及其数量
      cloneUse: "", //使用的回复模板
      problemType: "", //问题类型
      lessAndBroken: "", //缺件及破损情况
      association: "", //关联操作
      buysText: "", //买家消息内容
      charingTime: "", //收取时间
      whiteOrBlack: "", //黑白名单
      badOrGood: "", //好差评1
      buysInterpret: "", //买家回复翻译
      cloneInterpret: "", //使用回复模板

      serchType: ""
    };
  },
  methods: {
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.tableData = res.data.re_data;
        this.total = res.data.count[0].count / 5;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    NavClick(index, index2, item3) {
      this.serchType = "全部";
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          $(".emailTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 5;
        this.page = 1;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    getfilename(x) {
      //获取文件名函数
      let pos = x.lastIndexOf("/");
      return x.substring(pos + 1);
    },
    serchall() {
      //全部搜索按钮
      document.querySelector("footer").style.display = "none";
      this.serchType = "全部";
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav
        }
      }).then(res => {
        console.log(res);
        this.page = 1;
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          $(".emailTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 5;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    searchIn() {
      //站内搜索框
      this.serchType = "站内";
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav,
          type: this.serchType
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          $(".emailTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 5;
        this.page = 1;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    checkEmailDetail(order, urlLi) {
      //点击表格查看回复邮件详情
      if (!order) {
        document.querySelector("footer").style.display = "none";
      } else {
        document.querySelector("footer").style.display = "block";
        axios({
          url: this._url2 + "server/customer_management/email_manage_detail/",
          method: "get",
          params: {
            order_number: order
          }
        }).then(res => {
          console.log(res);
          if (res.data.data.length > 0) {
            this.orderId = res.data.data[0].order_number;
            this.site = res.data.data[0].site;
            this.country = res.data.data[0].country;
            this.goodAndMount = res.data.data[0].product_name;
            this.cloneUse = res.data.data[0].email_template;
            this.problemType = res.data.data[0].problem_type;
            this.lessAndBroken =
              res.data.data[0].lack_part +
              "         数量：" +
              res.data.data[0].damaged;
            this.association = res.data.data[0].related_operations;
            this.buysText = res.data.data[0].email_content;
            this.charingTime = res.data.data[0].upload_time;
            this.whiteOrBlack = res.data.data[0].customer_status;
            this.badOrGood = res.data.data[0].Praise;
            this.buysInterpret = res.data.data[0].customer_translation;
            this.cloneInterpret = res.data.data[0].translation_template;
            let str = "";
            console.log(urlLi, 555);
            if (urlLi) {
              for (let i = 0; i < urlLi.length; i++) {
                str += `<a href="${
                  urlLi[i]
                }" download target="_Blank">${this.getfilename(
                  urlLi[i]
                )}</a><br>`;
              }
            }

            document.querySelector(".accessory").innerHTML = str;
          }
        });
      }
    },
    searchOut() {
      document.querySelector("footer").style.display = "none";
      //查看站外邮件按钮
      this.serchType = "站外";
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav,
          type: this.serchType
        }
      }).then(res => {
        console.log(res);
        this.page = 1;
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          $(".emailTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 5;
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].accessory_addr) {
            this.tableData[i]["fileLI"] = this.tableData[
              i
            ].accessory_addr.split("@");
          }
        }
      });
    },
    closeFooter() {
      //关闭按钮
      document.querySelector("footer").style.display = "none";
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      if (this.serchType == "全部") {
        this.page = newPage;
        axios({
          url: this._url2 + "server/customer_management/email_manage",
          method: "get",
          params: {
            platform: this.firstNav,
            country: this.twiceNav,
            language: this.threeNav,
            page: this.page
          }
        }).then(res => {
          console.log(res);
          this.tableData = res.data.re_data;
          this.$nextTick(() => {
            $(".emailTable>tbody").scrollTop(0);
          });
          for (let i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].accessory_addr) {
              this.tableData[i]["fileLI"] = this.tableData[
                i
              ].accessory_addr.split("@");
            }
          }
        });
      } else {
        this.page = newPage;
        axios({
          url: this._url2 + "server/customer_management/email_manage",
          method: "get",
          params: {
            platform: this.firstNav,
            country: this.twiceNav,
            language: this.threeNav,
            page: this.page,
            type: this.serchType
          }
        }).then(res => {
          console.log(res);
          this.tableData = res.data.re_data;
          this.$nextTick(() => {
            $(".emailTable>tbody").scrollTop(0);
          });
          for (let i = 0; i < this.tableData.length; i++) {
            if (this.tableData[i].accessory_addr) {
              this.tableData[i]["fileLI"] = this.tableData[
                i
              ].accessory_addr.split("@");
            }
          }
        });
      }
    }
  },

  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data;
    });
  }
};
</script>

<style scoped>
table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 72vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

footer div {
  margin: 2vh 0 0 0;
}

.buysInterpret {
  position: absolute;
  width: 10vw;
  height: 20vh;
  border-radius: 10px;
  top: 26vh;
  left: 35vw;
}

.cloneInterpret {
  position: absolute;
  width: 10vw;
  height: 20vh;
  border-radius: 10px;
  top: 48vh;
  left: 35vw;
}

.accessory {
  position: absolute;
  width: 10vw;
  height: 20vh;
  border-radius: 10px;
  border: 2px solid #dddddd;
  top: 78vh;
  left: 35vw;
}

.leftNav {
  width: 12vw;
  height: 100vh;
  position: fixed;
  left: 0;
}

table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(3),
table tr td:nth-child(3),
table tr th:nth-child(4),
table tr td:nth-child(4),
table tr th:nth-child(5),
table tr td:nth-child(5),
table tr th:nth-child(11),
table tr td:nth-child(11),
table tr th:nth-child(12),
table tr td:nth-child(12) {
  width: 4vw;
}

table tr th:nth-child(2),
table tr td:nth-child(2),
table tr th:nth-child(8),
table tr td:nth-child(8),
table tr th:nth-child(10),
table tr td:nth-child(10) {
  width: 13vw;
}

table tr th:nth-child(6),
table tr td:nth-child(6) {
  width: 12vw;
}

table tr th:nth-child(7),
table tr td:nth-child(7),
table tr th:nth-child(9),
table tr td:nth-child(9) {
  width: 5.5vw;
}
</style>



